.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #333333;
  height: 50px;
  padding: 20px;
  color: #CFCDCD;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.topology {
  height: 80%;
}

.navbar {
  padding-top: 0px;
  padding-bottom:0px;
}

.login {
  font-size: .80em;
  margin-right:50px;
}
.logSearch {
  padding-top: 0px;
  margin-bottom: 20px;
}

#devnetLogo {
  margin-left: 15px;
  font-size: 30px;
  font-weight: bold;
  padding-top: -10px;
  padding-right: 15px;
}

#navList {
  padding-top:13px;
}




